/* –°—Ç–∏–ª–∏ –¥–ª—è —Å—Ç–∞—Ç–µ–π, –∞–Ω–∞–ª–æ–≥–∏—á–Ω—ã–µ —Å—Ç–∏–ª—è–º –¥–ª—è –ø–æ–¥–∫–∞—Å—Ç–æ–≤ */
.articles {
  display: flex;
  flex-direction: column;
  margin: 2rem;
  padding: 20px;
  background-color: #0a0a0ae6;
  color: var(--color-white);
  border-radius: 20px;
  box-shadow: 0 4px 8px var(--color-shadow);
}

.article-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  width: 100%;
}

.article {
  background-color: #4C4945;
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0 2px 4px var(--color-shadow);
}

.article-title {
  font-family: 'Header-TITLE', sans-serif;
  font-size: 30px;
  text-align: center;
}

.article h3 {
  font-family: 'Header-TITLE', sans-serif;
  font-size: 30px;
  text-align: center;
}

.article p {
  font-family: 'Times New Roman', Times, serif;
  font-size: 16px;
  line-height: 1.5;
  color: #D9BFA2;
}

/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 600px;
  width: 100%;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  color: #aaa;
  cursor: pointer;
}

.close-btn:hover {
  color: black;
}

.article {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0 2px 4px var(--color-shadow);
  min-height: 200px; /* –ù–∞—Å—Ç—Ä–æ–π—Ç–µ –≤—ã—Å–æ—Ç—É –±–ª–æ–∫–∞ */
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: white; /* –ß—Ç–æ–±—ã —Ç–µ–∫—Å—Ç –±—ã–ª –≤–∏–¥–µ–Ω –ø–æ–≤–µ—Ä—Ö –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
}

.modal-image {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
}